body {
  color: $oj-dark;
  font-family: 'SF', 'SF UI Text', 'Acumin Pro', 'Roboto', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 1.5rem;
  line-height: 2.25rem;
  -webkit-font-smoothing: antialiased;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-rendering: optimizeLegibility;
  margin-bottom: 1em;
  letter-spacing: 0.05em;
}
h1 {
  font-size: 2rem;
  line-height: 1.4em;
  font-weight: 700;
  font-style: normal;
  margin-bottom: 1em;
}
h2 {
  font-size: 1.8rem;
  line-height: 1.4em;
  font-weight: 700;
  font-style: normal;
  margin-bottom: 0.8em;
}
h3 {
  font-size: 1.2rem;
  line-height: 1.4em;
  font-weight: 700;
  font-style: normal;
  margin-bottom: 0.4em;
}
h4 {
  font-size: 1rem;
  line-height: 1.4em;
  font-weight: 700;
  font-style: normal;
  margin-bottom: 0.4em;
}
h5 {
  font-size: 1rem;
  line-height: 1.4em;
  font-weight: 700;
  font-style: normal;
  margin-bottom: 0.4em;
}
h6 {
  font-size: 1rem;
  line-height: 1.4em;
  font-weight: 700;
  font-style: normal;
  margin-bottom: 0.4em;
}
p {
  margin: 0 0 1.5rem;
}
a {
  color: inherit;
  text-decoration: none;
}
em {
  font-style: italic;
}
strong {
  font-weight: 700;
}
img {
  margin: 0;
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}
table {
  border-spacing: 0;
  border-collapse: separate;
  border: 1px solid #cbcbcb;
  text-align: left;
  margin: 1em 0;
  width: 100%;
}
table caption {
  font-weight: 700;
}
table thead,
table tfoot,
table tbody,
table tr {
  border-bottom: 1px solid #cbcbcb;
}
table th {
  font-weight: 700;
  padding: 10px;
}
table td {
  padding: 10px;
}
ul,
ol {
  padding-left: 2.25rem;
  line-height: 2.25rem;
  letter-spacing: 0.02em;
  margin-bottom: 2em;
  margin-left: 0.6em;
}
ul ol,
ol ol,
ul ul,
ol ul {
  font-size: 1em;
}
ul {
  list-style-type: disc;
}
ol {
  list-style-type: decimal;
}
ol ol {
  list-style-type: lower-alpha;
}
abbr,
acronym {
  border-bottom: 1px dotted #000;
  cursor: help;
}
address {
  display: block;
  margin: 0 0 2.25rem/2;
}
hgroup {
  margin-bottom: 2.25rem/2;
}
del {
  color: lighten $oj-dark, 15%;
}
pre {
  overflow: auto;
  display: block;
  font-size: 1.2rem;
  line-height: 1.4em;
  font-family: monospace;
  padding: 1em;
  border-left: 1px solid $light;
  margin: 0.5em 0;
  background-color: #eee;
}
hr {
  border-bottom: 1px solid $base_border_color;
  border-left: none;
  border-right: none;
  border-top: none;
  margin: 2.25rem 0;
}
blockquote {
  font-style: italic;
  background-repeat: no-repeat;
  background-position: top left;
}
cite {
  margin-top: 1em;
  display: inline-block;
}
cite:before {
  content: '\2014 \00A0';
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  padding: 0;
  border: 0;
}
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  border: none;
  cursor: pointer;
}
@keyframes rotate {
0% {
    transform: rotate(0deg);
}
100% {
    transform: rotate(360deg);
}
}
fieldset {
  border: none;
  padding: 0;
}
input,
label,
select {
  display: block;
}
textarea,
input[type="color"],
input[type="date"],
input[type="datetime"],
input[type="datetime-local"],
input[type="email"],
input[type="month"],
input[type="number"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="text"],
input[type="time"],
input[type="url"],
input[type="week"] {
  color: #000;
  background-color: #fff;
  border: none;
  font-family: 'SF', 'SF UI Text', 'Acumin Pro', 'Roboto', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 400;
  width: 100%;
}
textarea.hollow,
input[type="color"].hollow,
input[type="date"].hollow,
input[type="datetime"].hollow,
input[type="datetime-local"].hollow,
input[type="email"].hollow,
input[type="month"].hollow,
input[type="number"].hollow,
input[type="password"].hollow,
input[type="search"].hollow,
input[type="tel"].hollow,
input[type="text"].hollow,
input[type="time"].hollow,
input[type="url"].hollow,
input[type="week"].hollow {
  border: 0;
  box-shadow: none;
}
textarea:hover,
input[type="color"]:hover,
input[type="date"]:hover,
input[type="datetime"]:hover,
input[type="datetime-local"]:hover,
input[type="email"]:hover,
input[type="month"]:hover,
input[type="number"]:hover,
input[type="password"]:hover,
input[type="search"]:hover,
input[type="tel"]:hover,
input[type="text"]:hover,
input[type="time"]:hover,
input[type="url"]:hover,
input[type="week"]:hover {
  border-color: rgba(203,203,203,0.5);
}
textarea:focus,
input[type="color"]:focus,
input[type="date"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="email"]:focus,
input[type="month"]:focus,
input[type="number"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="tel"]:focus,
input[type="text"]:focus,
input[type="time"]:focus,
input[type="url"]:focus,
input[type="week"]:focus {
  border-color: rgba(203,203,203,0.5);
  box-shadow: none;
  outline: none;
}
textarea:disabled,
input[type="color"]:disabled,
input[type="date"]:disabled,
input[type="datetime"]:disabled,
input[type="datetime-local"]:disabled,
input[type="email"]:disabled,
input[type="month"]:disabled,
input[type="number"]:disabled,
input[type="password"]:disabled,
input[type="search"]:disabled,
input[type="tel"]:disabled,
input[type="text"]:disabled,
input[type="time"]:disabled,
input[type="url"]:disabled,
input[type="week"]:disabled {
  opacity: 0.9;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=90)";
  filter: alpha(opacity=90);
  background-color: rgba(0,0,0,0.1);
  border: none;
  box-shadow: none;
}
textarea {
  resize: vertical;
  min-height: 8em;
}
input[type="submit"] {
  visibility: hidden;
  display: none;
}
input[type="checkbox"],
input[type="radio"] {
  display: inline;
}
input[type="file"],
select {
  width: auto;
  max-width: 100%;
  margin-bottom: $base-line-height;
}
html {
  font-size: 14px;
}
@media only screen and (max-width: 1500px) {
html {
    font-size: 12px;
}
}
@media only screen and (max-width: 1000px) {
html {
    font-size: 11px;
}
}
@media only screen and (max-width: 800px) {
html {
    font-size: 9px;
}
}
@media only screen and (max-width: 600px) {
html {
    font-size: 8px;
}
}
* {
  box-sizing: border-box;
}
#top-bar {
  position: fixed;
  top: 0;
  z-index: 9;
  width: 100%;
  background-color: #fff;
  padding: 1rem 2rem;
}


