
#font-tester .settings {
  display: -ms-flexbox;
  display: box;
  display: flex;
  -webkit-box-orient: horizontal;
  -moz-box-orient: horizontal;
  -o-box-orient: horizontal;
  -webkit-box-lines: single;
  -moz-box-lines: single;
  -o-box-lines: single;
  -ms-flex-flow: row nowrap;
  flex-flow: row nowrap;
  -o-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
}
#font-tester .settings .settings-section {
  display: block;
  zoom: 1;
}
#font-tester .settings .settings-section:before,
#font-tester .settings .settings-section:after {
  content: "";
  display: table;
}
#font-tester .settings .settings-section:after {
  clear: both;
}
#font-tester .settings .setting-list {
  float: left;
  margin-right: 2rem;
}
#font-tester .settings .setting-link {
  font-size: 1rem;
  font-weight: 200;
  line-height: 1rem;
  padding: 0.5em 0;
  display: block;
  text-decoration: underline;
  cursor: pointer;
}
#font-tester .settings .setting-button {
  display: inline-block;
  width: 2rem;
  height: 2rem;
  margin-left: 1rem;
  cursor: pointer;
  background: center center no-repeat;
  background-size: 1.5rem;
  border-radius: 3px;
}
#font-tester .settings #choose-uppercase {
  background-image: url("/static/images/ui/case-upper.svg");
}
#font-tester .settings #choose-none {
  background-image: url("/static/images/ui/case-none.svg");
}
#font-tester .settings #choose-left {
  background-image: url("/static/images/ui/align-left.svg");
}
#font-tester .settings #choose-center {
  background-image: url("/static/images/ui/align-center.svg");
}
#font-tester .settings #choose-right {
  background-image: url("/static/images/ui/align-right.svg");
}
#font-tester .test-area {
  border: none;
  line-height: 1.4em;
  margin-top: 2rem;
}


